.header {
  max-width: 880px;
  margin: 0 auto;
  padding-top: 45px;
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 1023px) {
  .header {
    width: calc(100% - 2 * 20px);
  }
}

@media (max-width: 767px) {
  .header {
    width: 100%;
    padding-top: 28px;
    padding-bottom: 32px;
  }
}